import About from "./About";
import Alert from "./Alert";
import "./App.css";
import Navbar from "./Navbar";
import TextForm from "./TextForm";
import React, { useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

function App() {
  const [alert, setAlert] = useState(null);
  const [mode, setMode] = useState("light");
  //  const [key,setKey]=useState(1);

  const [color,setColor]=useState(
   
    {
        color: 'black',
        background: 'white',
        border: '1.65px solid',
        borderColor: 'white'
    }

);


  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type,
    });
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  };



  const toggleMode = () => {
    if (mode === "light") {
      setMode("dark");
      document.body.style.backgroundColor = "#495057";
      document.body.style.color = "white";

      setColor(
        {
          color: 'white',
          background: 'black',
          border: '1.65px solid',
          borderColor: 'white'
        }
      );

      showAlert("Dark Mode has been enabled", "success");
      document.title = "Text Editor - Dark Mode";
    } else if (mode === "dark") {
      setMode("light");
      document.body.style.backgroundColor = "white";
      document.body.style.color = "black";

      setColor(
          {
            color: 'black',
            background: 'white',
            border: '1.65px solid',
            borderColor: 'black'
          }
      );

      showAlert("Light Mode has been enabled", "success");
    }
  };

  return (
    <>
      <Router>
        <Navbar title="Text Editor" mode={mode} toggleMode={toggleMode} />
        <Alert alert={alert} showAlert={showAlert} />
        {/*<TextForm heading="Enter text here" mode={mode} showAlert={showAlert} />
         */}{" "}
        <Routes>
          <Route exact path="/About" element={<About color={color} />} />
          <Route
            exact path="/"
            element={<TextForm mode={mode} showAlert={showAlert} heading="Enter the text below" />}
          />
        </Routes>
      </Router>
    </>
  );
}

export default App;
