import React, { useState } from "react";
import { PDFDocument, StandardFonts } from 'pdf-lib';


export default function TextForm(props) {
  const [text, setText] = useState("");

  const downloadText = async () => {
    const element = document.createElement('a');
    const pdfData = await generatePdfData();
    const file = new Blob([pdfData], { type: 'application/pdf' });
    element.href = URL.createObjectURL(file);
    element.download = 'document.pdf';
    document.body.appendChild(element);
    element.click();
  };


  const generatePdfData = async () => {
    const pdfDoc = await PDFDocument.create();
    const page = pdfDoc.addPage();
    const TimesRoman = await pdfDoc.embedFont(StandardFonts.TimesRoman);

    const { width, height } = page.getSize();
    const fontSize = 12;
    const textWidth = TimesRoman.widthOfTextAtSize(text, fontSize);
    const textHeight = TimesRoman.heightAtSize(fontSize);

    page.drawText(text, {
      x: (width - textWidth) / 2,
      y: (height - textHeight) / 2,
      size: fontSize,
      font: TimesRoman,
    });

    return await pdfDoc.save();
  };

  function handleUpperCase() {
    let newText = text.toLocaleUpperCase();
    setText(newText);
    props.showAlert("Converted to UpperCase", "success");
  }

  function handleLowerCase() {
    let newText = text.toLocaleLowerCase();
    setText(newText);
    props.showAlert("Converted to LowerCase", "success");
  }

  function handleOnChange(e) {
    setText(e.target.value);
  }

  function handleClearText() {
    setText("");
    props.showAlert("Text Cleared", "success");
  }

  function handleCopy() {
    var text = document.getElementById("myBox");
    text.select();
    navigator.clipboard.writeText(text.value);
    props.showAlert("Copied to Clipboard", "success");
  }

  function handleExtraSpace() {
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
    props.showAlert("Extra Space Removed", "success");
  }

  return (
    <>
      <div>
        <h1>{props.heading} </h1>
        <div
          id='#report'
          className="mb-3"
          style={
            props.mode === "light"
              ? {
                  color: "black",
                  background: "white",
                }
              : {
                  color: "white",
                  background: "rgb(5 24 83)",
                }
          }
        >
          <textarea
            className="form-control mx-10000 my-20000"
            value={text}
            style={
              props.mode === "light"
                ? {
                    color: "black",
                    background: "white",
                  }
                : {
                    color: "white",
                    background: "#6c757d",
                  }
            }
            onChange={handleOnChange}
            id="myBox"
            rows="4"
          ></textarea>
        </div>
        <button className="btn btn-primary mx-2" onClick={handleUpperCase}>
          Convert to Uppercase
        </button>
        <button className="btn btn-primary mx-2" onClick={handleLowerCase}>
          Convert to Lowercase
        </button>
        <button className="btn btn-primary mx-2" onClick={handleClearText}>
          Clear Text
        </button>
        <button className="btn btn-primary mx-2" onClick={handleCopy}>
          Copy
        </button>
        <button className="btn btn-primary mx-2" onClick={handleExtraSpace}>
          Handle Extra Space
        </button>
        <button className="btn btn-primary mx-2" onClick={downloadText}>
        Download
      </button>
      </div>
      <div className="container my-2">
        <h1>Text Summary</h1>
        <p>
          {
            text.split(" ").filter((element) => {
              return element.length !== 0;
            }).length
          }{" "}
          words and {text.length} characters
        </p>
        <h2>Preview</h2>
        <p>{text.length > 0 ? text : "Enter text to Preview here"}</p>
        <p>
          {0.008 *
            text.split(" ").filter((element) => {
              return element.length !== 0;
            }).length}{" "}
          Minutes Read
        </p>
      </div>
    </>
  );
}
